/* Global */

* {
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji,
    Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}

/* Chats Page */

.chats-page {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
}

.nav-bar {
  width: 100%;
  height: 65px;
  background-image: linear-gradient(260deg, #112037 0%, #432777 100%);
}
::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}
.logo{
  padding: 2.2vh 0.8vw;
  text-align: left;
  width: 20vw;
}

.logo img{
  height:30px;
  width: 30px;
}
.logo-tab {
  font-family: 'myFirstFont';
 
 
  
  /* font-family: 'Georgia', 'serif', 'Arial Narrow', Arial; */
  position: absolute;
  left: 52px;
  top: 15px;
  font-size: 32px;
  font-weight: 700;
  color: rgb(250, 244, 244);
}

.logout-tab {
  position: absolute;
  top: 18px;
  right: 22px;
  color: rgb(238, 229, 218);
  cursor: pointer;
}

.logout-ta {
  position: absolute;
  top: 18px;
  right: 120px;
  color: rgb(151, 171, 201);
  cursor: pointer;
}
.logout-t {
  position: absolute;
  top: 18px;
  right: 220px;
  color: rgb(146, 228, 194);
  cursor: pointer;
}
::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}

/* Login Page */

#login-page {
  padding: 100px;
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(https://coolwallpapers.me/th700/5739499-black-mountain-wallpapers.jpg);
 

}

#login-card {
  position: relative;
  top: calc(50vh - 144px);
  left: calc(50vw - 210px);
  padding-top: 36px;
  padding-bottom: 66px;
  width: 420px;
  text-align: center;
  /* background-color: white; */
  border-radius: 22px;
  position: absolute;
  background: rgba(3, 3, 3, 0.25);
  box-shadow: 1px 1px 50px #000;
  color: white;
  
}

.login-button {
  cursor: pointer;
  color: white;
  padding: 12px;
  border-radius: 8px;
  display: inline-block;
}

.facebook {
  background-color: #3b5998;
}

.google {
  background-color: #4285f4;
  color: white;
}

#loading-container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgb(255, 255, 255, 0.22);
}

#loading-icon {
  font-size: 66px;
  position: relative;
  left: calc(50vw - 33px);
  top: calc(50vh - 22px);
}
#root, html, body {
  height: 100vh;
}

body {
  margin: 0;
  background-color: rgb(112, 119, 221);
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}
   
.shadow {
  box-shadow: 0 2px 15px rgb(0 0 0 / 68%);
}

/* Chat List */
.ce-wrapper {
  background-color: #3b5086b0 !important;
}

.ce-chats-container {
  background-color: #112037 !important;
  border-radius: 0px !important;
}

.ce-chat-form-container {
  background-color: #112037 !important;
  color: rgb(94, 149, 194);
}

.ce-chat-card {
  margin: 0px 6px !important;
  color: rgb(94, 149, 194);
}

.ce-chat-card-loading-bar {
  background-color: #535a6d !important;
}

.ce-active-chat-card {
  background-color: #726dfe !important;
}

.ce-chat-title-text {
  color: rgb(255, 255, 255) !important;
}

.ce-chat-subtitle-text {
  color: hsla(0, 0%, 100%, 0.568) !important;
}

.ce-chat-unread-dot {
  background-color: rgb(211, 99, 99) !important;
}

/* Chat Feed */


.ce-chat-title-container {
  background-color: #14294B !important;
  border-radius: 0px !important;
}

.ce-chat-feed-container {
  background-color: #14294B !important;
}

.ce-primary-button {
  background-color: #9a97fc !important;
}

.ce-message-date-text {
  color: hsla(0, 0%, 100%, 0.568) !important;
}

.ce-my-message-bubble {
  background-color: #726dfe !important;
}

.ce-my-message-sinding-bubble {
  background-color: #938ffe !important;
}

.ce-my-message-timestamp {
  color: #9a97fc !important;
}

.ce-their-message-timestamp {
  color: hsla(0, 0%, 100%, 0.568) !important;
}

#ce-send-message-button {
  background-color: #14294B !important;
}

#ce-ice-breaker-gif {
  max-width: 115px !important;
  margin-top: 30px !important;
}

.ce-social-message-form {
  background-color: #14294B !important;
}

.ce-message-input-form {
  padding-bottom: 10px !important;
 
}

#msg-textarea {
  width: calc(100% - 140px) !important;
  position: relative !important;
  top: 4px !important;
  border-width: 0px !important;
  background-color: #14294B !important;
  color: rgb(29, 84, 116) !important;
}
#msg-textarea::placeholder {
  color: hsla(0, 0%, 100%, 0.568) !important;
}

#ce-upload-document-icon {
  color: white !important;
}

.ce-feed-container-bottom {
  height: "44px";
}

.ce-message-images-row {
  background-color: #4a5162 !important;
}

.ce-their-message-sender {
  color: hsla(0, 0%, 100%, 0.568) !important;
}

.ce-their-message-bubble {
  background-color: #535a6d !important;
  color: rgba(255, 255, 255, 0.705) !important;
}

/* Chat Settings */

.ce-settings {
  background-color: #112037 !important;
  border-radius: 0px !important;
}

.ce-chat-title-form .ce-text-input {
  background-color: #112037;
  color: white;
}

.ce-section-title-container {
  background-color: #112037 !important;
  color: white;
}

#ce-options-drop-down {
  background-color: #112037 !important;
  color: white;
}

.ce-person-text {
  color: hsla(0, 0%, 100%, 0.568);
}

.ce-danger-button {
  background-color: #112037 !important;
  border: 1px solid #9a97fc !important;
  color: #9a97fc !important;
}

.ce-autocomplete-close {
  background-color: white !important;
  border-radius: 0px !important;
}
